.skillcontent {
    display: flex;
    justify-content: center;
    text-align: left;
    margin: {
        top: auto;
        bottom: 40px;
        left: auto;
        right: auto;
    }
    max-width: 950px;
    .list {
        display: flex;
        justify-content: center;
        line-height: 1.8;
        flex: 1.5;
    }
    .desc { 
        flex: 2.5;
        margin: {
            left: 20px;
            right: 20px;
        }
    }
    @media only screen and ( max-width: 700px ) {
        display: block;
    }
    dl {
        dt {
            font-weight: 500;
            font-size: 1.3rem;
        }

        dd {
            margin-left: 0;
            &.experience {
                color: #666666;
                margin-bottom: 20px;
            }
        }
    }
    ul {
        list-style: none;
        padding-left: 0;
        li {
            text-indent: 10px;
            margin-bottom: 20px;
            text-align: justify;
            word-break: break-all;
        }
    }
}