.awards-content {
    max-width: 530px;
    margin: 0 auto 20px auto;
    padding: 15px;
    border: 1px solid #dcdcdc;
    background-color: white;
    text-align:left;

    h3 {
        display: block;
        font-weight: 400;
        font-size: 1rem;
        margin: auto;
        padding-left: 15px;
        padding-bottom: 10px;
        border-bottom: 1px solid #dcdcdc;
    }
    h4 {
        margin: 15px auto auto auto;
        font-weight: 500;
    }

    span {
        display: block;
        font-size: 0.9rem;
        &.host {
            color: #555555;
            font-size: 0.7rem;
            margin-bottom: 5px;
        }
    }

    dd {
        margin: 0;
        padding: 0 0 0 10px;
        color: #000000;
        font-size: 0.9rem;
        &:before  {
            transform: translateX(-8px);
            position: absolute;
            content: '· '
        }
        span.date {
            margin-right: 10px;
        }
    }
    table {
        font-size: 0.8rem;
        td {
            padding-right: 10px;
            min-width: 36px;
        }
    }
}

.divide {
    margin: 10px auto 10px auto;
    height: 1px;
    background: #E9E9E9;
}

.devexp-timeline {
    position: relative;
    margin: {
        left: auto;
        right: auto;
    }
    max-width: 1000px;

    .timeline {
        &-linebase {
            position: absolute;
            left:250px;
            background: #3498DB;
            width: 3px;
            height: 100%;
        }
        &-lineend {
            @extend .timeline-linebase;
            top: 100%;
            height: 50px;
            background: linear-gradient(to bottom, #3498D8, rgba(52,152,219,0));
        }
        &-dot {
            position: absolute;
            top: 0;
            left: 244.5px;
            z-index: 1;
            width: 9px;
            height: 9px;
            background: white;
            border: 2px solid #3498D8;
            border-radius: 50%;
        }
        &-branch {
            position: absolute;
            left: 250px;
            width: 30px;
            height: 3px;
            background: #3498D8;
        }
        &-date {
            position: absolute;
            left: 20px;
            padding-bottom: 10px;
            width: 220px;
            text-align: right;
            font-size: 1.2rem;
            text-indent: 10px;
            font-weight: 300;
            transform: translateY(-13px);
        }
        &-office {
            width: 220px;
            margin-bottom: 30px;
            padding: 15px;
            background: white;
            border: 1px solid #E9E9E9;
            border-radius: 6px;
            text-align: right;
            cursor: pointer;
            
            h {
                &3 {
                    font-weight: 400;
                    font-size: 1.5rem;
                    margin: auto auto 6px auto;
                }
                &4 {
                    margin: auto auto 10px auto;
                    font-size: 0.85rem;
                }
                &5 {
                    margin-top: 0;
                    margin-bottom: 10px;
                    font-size: 1rem;
                    font-weight: 400;
                    text-indent: 5px;
                }
            }
        }
        &-itemlist {
            &-open { 
                transition-duration: 0.5s;
            }
            &-close {
                display: none;
                transition-duration: 1s;
            }
        }
        &-item {
            text-align: left;
            margin-left: 280px;
            margin-top: 30px;
            margin-bottom: 30px;
            max-width: 500px;
            padding: 20px;
            background: white;
            border: 1px solid #E6E6E6;
            border-radius: 0 5px 5px 5px;

            h {
                &3 {
                    margin: auto;
                    font-size: 1.08rem;
                    font-weight: 400;
                }
                &4 { 
                    margin: auto;
                    font-size: 0.8rem;
                    text-indent: 5px;
                    color: #555555
                }
            }
        }
    }
    @media only screen and ( max-width: 630px ) {
        .timeline {
            &-linebase, &-branch { left: 10px; }
            &-dot { left: 4.5px; }
            &-item { margin-left:40px; }
            &-office { margin-left: 10px; text-align: left; }
            &-date {
                position: inherit;
                margin-bottom: 10px;
                width: auto;
                text-align: left;
                border-bottom: 1px solid #E6E6E6;
                transform: none;
            }
        }
    }
}