.projectlist {
    .project {
        display: flex;
        margin: 0 auto 40px auto;
        padding: 20px;
        max-width: 850px;
        background-color: white;
        border-radius: 7px;
        border: 1px solid #F0F0F0;
        text-align: left;
        @media only screen and ( max-width: 870px ) {
            display: block;
            width: 384px;
        }
        @media only screen and ( max-width: 500px ) {
            width: auto;
        }
        .image {
            display: block;
            border: 1px solid rgba(0,0,0,0.1);
            flex-shrink: 0;
            @media only screen and ( max-width: 500px ) {
                width: 100%;
                height: auto;
            }
        }

        .detail {
            position: relative;
            margin-left: 20px;
            @media only screen and ( max-width: 870px ) {
                margin: 20px auto auto auto;
            }
            h3 {
                margin: auto;
                font-weight: 400;
                font-size: 1.5rem;
            }
            .techlist {
                margin: 2px auto 10px auto;
                text-indent: 0;
                .techshild {
                    display: inline-block;
                    margin-top: 3px;
                    margin-right: 10px;
                    padding: 3px 5px;
                    font-size: 0.7rem;
                    font-weight: 700;
                    background-color: #3498db;
                    color: white;
                    border-radius: 5px;
                }
            }
            h4 {
                margin: auto; 
            }
            .dialog-toggle {
                position: absolute;
                bottom: -10px;
                text-indent: inherit;
                width: 100%;
                // text-align: center;
                // vertical-align: middle;
                // color: #3498db;
                // font-style: italic;
                @media only screen and ( max-width: 870px ) {
                    position: relative;
                }
                // &:hover {
                //     background-color: #F0F0F0;
                // }
                // span {
                //     cursor: pointer;
                //     line-height: 1.4;
                //     transform: translateY(2px);
                // }
            }
        }
    }
}

.dialog {
    &-close-button {
        position: fixed;
        right: 5%;
        font-size: 3rem;
        transform: scaleX(1.5);
        cursor: pointer;
    }
    &-top {
        // display: flex;
        // @media only screen and ( max-width: 850px ) {
        //     display: block;
        // }
        img {
            flex-shrink: 0;
            display: block;
            border: 1px solid rgba(0,0,0,0.1);
            // @media only screen and ( max-width: 850px ) {
                width: 100%;
                height: auto;
            // }
        }
        &-info {
            // list-style-type: none;
            // li span {
            //     display: inline-block;
            //     margin: auto 20px auto auto;
            //     min-width: 100px;
            //     text-align: right;
            //     word-break: keep-all;
            // }
            padding-left: 10px;
            h3 {
                margin: 20px 20px auto auto;
                font-weight: 500;
                font-size: 1.4rem;
            }
            h4 {
                margin: auto 20px 20px auto;
                color: #999999;
            }
            tr td {
                padding: auto;
                
                &.name {
                    width: 60px;
                    padding-right: 20px;
                    text-align: right;
                    vertical-align: text-top;
                }
                a {
                    text-decoration: none;
                    color: #3498db;
                    &:visited {
                        color: #3498db;
                    }
                }
            }
        }
    }
    &-content {
        padding: 10px;
        .divider {
            height: 1px;
            background-color: #999999;
        }
        p, ul {
            line-height: 2;
            @media only screen and ( max-width: 500px ) {
                line-height: 1.7;
                font-size: 14px;
            }
            a {
                text-decoration: none;
                color: #3498db;
                &:visited {
                    color: #3498db;
                }
            }
        }
    }
}