.about-content {
    display: flex;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 50px;
    padding-left: 60px;
    padding-right: 60px;
    max-width: 900px;

    .profile {
        margin-right: 70px;
        flex-shrink: 0;
        p {
            text-align:center;
            img {
                transform: translate( -5px, 6px);
            }
        }
    }
}

@media only screen and ( max-width: 900px ) {
    .about-content {
        display: block;
        padding-left: 60px;
        padding-right: 60px;
        .profile {
            margin-right: auto;
        }
        
        .introduce {
            margin-top: 30px;
        }
    }
}

@media only screen and ( max-width: 600px ) {
    .about-content {
        padding-left: 30px;
        padding-right: 30px;
    }
}