.title {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    margin: 0;
    height: 100vh;
    background: url('../../image/Title.PNG');
    background-size: cover;
    .filter {
        position: absolute;
        top: 0;
        left: 0;
        margin: 0;
        width: 100%;
        height: 100vh;
        background-color: rgba(22,22,22,0.8);
    }
    .content {
        z-index: 1;
        text-align: center;
        color: white;
        h1, h3{ margin: 10px; }
        h1 {
            font-size: 5rem;
        }
        h3 {
            font-size: 2rem;
        }
    }
}

@media only screen and ( max-width: 1000px ) {
    .title {
        height: 40vh;
        
        .filter {
            height: 40vh;
        }
        
        .content {
            h1 {
                font-size: 2rem;
            }
            h3 {
                font-size: 1rem;
            }
        }
    }
}